.slider{
    background-color: #a82d49;
    border:  1px solid #a82d49;
    width: 100%;
    max-width: 100%;
    text-align: center;
    padding: 50px 0px;
}
.slider h2{
    color: #fff;
}
.slider img{
    border-radius: 50%;
    width: 100px;
    height: 100px;
    margin-bottom: 200PX;
}

.slider .carousel-caption P{
    color: #212121 !important;
    font-size: 17px;
    line-height: 30px;

}
.slider .carousel-caption h3{
    color: #a82d49 !important;
    display: block;
    margin: 15px 0px;
    text-transform: uppercase;
    font-size: 17px;

}
.slider .carousel{
    background-color: #fff;
    box-sizing: border-box;
    border-radius: 10px;
    margin: 40px 0px;
    padding: 25px 0px;
}
.carousel-indicators {
    position: absolute;
    right: 0;
    bottom: 0;
    left: 0;
    /* top: 50px; */
    z-index: 15;
    display: flex;
    justify-content: center;
    padding-left: 0;
    margin-right: 15%;
    margin-left: 15%;
    list-style: none;
    
}.slider .carousel-indicators  button{
    width: 15px;
    height: 15px;
    background-color: #FFF;
    border: 1px solid #a82d49;
    border-radius: 50%;
    margin: 5px 5px 15px 5px;
}
